//effect
//レジスター
.reges_toggle_effect{
    animation-name:leftToright01;
    animation-duration:0.5s;
    animation-fill-mode: forwards;
    transform: scale(1);
}
.reges_toggle_effect_de{
    animation-name:rightToleft02;
    animation-duration:0.5s;
    animation-fill-mode: forwards;
    pointer-events: none;
    transform: scale(0);
    display:none;
}
//表示非表示
.toggle_effect{
    animation-name:rightToleft01;
    animation-duration:0.5s;
    animation-fill-mode: forwards;
    display:flex;
    transform: scale(1);
}
.toggle_effect_de{
    animation-name:rightToleftBack01;
    animation-duration:0.5s;
    animation-fill-mode: forwards;
    pointer-events: none;
    transform: scale(0);
    display:none;
}
.popup_toggle_effect{
    animation-name:leftToright01;
    animation-duration:0.5s;
    animation-fill-mode: forwards;
    transform: scale(1);
}
.popup_toggle_effect_de{
    animation-name:leftTorightBack02;
    animation-duration:0.5s;
    animation-fill-mode: forwards;
    pointer-events: none;
    transform: scale(0);
}
@keyframes leftToright01 {
    0% {
        opacity: 0;
        transform: translateX(-10px);
    }
    100%{
        opacity: 1;
        transform: translateX(0);
    }
}
@keyframes leftTorightBack01 {
    0% {
        opacity: 1;
        transform: translateX(0px);
    }
    100%{
        opacity: 0;
        transform: translateX(-10px);
    }
}
@keyframes leftTorightBack02 {
    0% {
        opacity: 1;
        transform: translateX(0px);
    }
    50%{
        opacity: 0;
        transform: translateX(-10px);
    }
    100%{
        opacity: 0;
        transform: translateX(-10px);
        height:0;
    }
}

@keyframes rightToleft01 {
    0% {
        opacity: 0;
        transform: translateX(-10px);
    }
    100%{
        opacity: 1;
        transform: translateX(0);
    }
}
@keyframes rightToleft02 {
    0% {
        opacity: 1;
        transform: translateX(0px);
    }
    100%{
        opacity: 0;
        transform: translateX(10px);
        height:0;
        ovarflow:hidden;
        display:block;
    }
}

